export default {
	data() {
		return {
			loading: false,
			email: '',
			password: ''
		}
	},
	computed: {
		disabled() {
			if (this.email || this.password) {
				return false
			}
			return true
		}
	},
	methods: {
		// 检验
		check() {
			if (!this.email || this.email === '') {
				uni.showToast({
					title: '邮箱不能为空!',
					icon: 'none'
				})
				return false
			}
			
			// 验证邮箱格式是否正确
			let ePattern = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;
			if (!ePattern.test(this.email)) {
				uni.showToast({
					title: '邮箱格式错误!',
					icon: 'none'
				})
				return false
			}
			
			if (!this.password || this.password === '') {
				uni.showToast({
					title: '密码不能为空!',
					icon: 'none'
				})
				return false
			}
			
			return true
		},
		// 提交事件
		complete() {
			this.loading = true
			this.disabled = true
			if (!this.check()) {
				this.loading = false
				this.disabled = false
				return
			}
			
			setTimeout(() => {
				uni.showToast({
					title: '提交成功！'
				})
				this.loading = false
				this.disabled = false
			}, 2000)
			
		}
	}
}
